<template>
  <div class="App" v-bind:class="{ 'comment-mask': CommentOpen }">
    <Login v-if="LoggedIn === false || LoggingIn === true"></Login>
    <TeamMembers v-if="LoggedIn === true && IsTeamMemberChosen === false"></TeamMembers>
    <ScoreInput v-if="IsTeamMemberChosen === true"></ScoreInput>
  </div>  
</template>

<script>
import axios from 'axios'
import store from './store'

import Login from './components/Login.vue'
import TeamMembers from './components/TeamMembers.vue'
import ScoreInput from './components/ScoreInput.vue'

import TeamMember from './models/TeamMember';

export default {
  data() {
    return {
      // ChosenTeamMemberChanged
    }
  },
  created: function () {
    this.getTeamMembers();
  },

  store,

  components: {
    Login,
    TeamMembers,
    ScoreInput
  },
  computed: {
    LoggedIn: {
      get () {
        return this.$store.state.LoggedIn;
      },
      set (value) {
        // console.log /g(value);
        this.$store.commit('setLoggedIn', value);
      }
    },
    LoggingIn: {
      get () {
        return this.$store.state.LoggingIn;
      }
    },
    IsTeamMemberChosen: {      
      get () {
        // console.log /g("IsTEAMMemberChosen changed");
        return this.$store.state.IsTeamMemberChosen;
      }
    },
    CommentOpen: {
      get () {
        return this.$store.state.CommentOpen;
      }
    }
  },
  methods: {
    getTeamMembers () {
      axios.get('http://localhost:3000/sql/apps')
      .then(res => {
        console.log(res.data);        
        TeamMember.insert( {data: res.data} );
        // console.log /g(TeamMember.all());
        // console.log /g(store.state.entities.teamMembers);
        // // // console.log /g(this.$store.state.TeamMembers);
      })
      .catch(res => {
        alert(res);
      });
    }
  },  
}
</script>

<style>
  .noScore {
    color: red
  }

  .scoreSaved {
    color: orange;    
  }

  .scoreSubmitted {
    color: green
  }

  .teamMemberAbsent {
    color: black
  }

  .comment-mask {
    position: fixed;
    z-index: 9999;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .5);
    display: table;
    transition: opacity .3s ease;
  }

  .comment-container {
    width: 300px;    
    padding: 20px 30px;
    background-color: #fff;
    border-radius: 2px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
    transition: all .3s ease;
    font-family: Helvetica, Arial, sans-serif;
  }

  body {
    font-family: Helvetica, Arial, sans-serif;
  }

  table,td {
    border:1px solid black;
  }
</style>


